// Gallery.module.scss
@import '/src/styles/define.scss';

.gallery {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: space-between; /* Horizontally center the image */
  align-items: center;     /* Vertically center the image */
  .title{
    margin: 20px 0px;
    font-size: 2em;
  }

  .imgBox {
    display: grid;
    justify-items: center; 
    justify-content: center; 
    text-align: center;   
    padding: 1dvh 2dvw;
    width: 100dvw;
    height: 50dvh;
    overflow-y: auto;
    overflow-x: hidden;
    grid-template-columns: repeat(auto-fill, minmax(31dvw, 1fr)); /* Makes each image take up 30vw, auto wrap to next line */
    gap: 1dvw;

    .imgCol {
      display: flex;
      position: relative;
      cursor: pointer;
      width: 31dvw;
      height: auto;
      display: flex;
      justify-content: center; /* Horizontally center image inside imgCol */
      align-items: center;     /* Vertically center image inside imgCol */
      .imgType{
        width: 90%;
        height: 90%;
      }

      &:hover img {
        transform: scale(1.05);
      }
    }

  }

  .modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  
    .modalContent {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
  
      img {
        max-width: 80%;
        max-height: 80vh;
        border-radius: 8px;
      }
  
      .closeBtn {
        position: fixed;
        top: 30px;
        right: 20px;
        font-size: 30px;
        color: white;
        cursor: pointer;
        background: transparent;
        border: none;
      }
  
      .prevBtn,
      .nextBtn {
        position: fixed;
        top: 50%;
        transform: translateY(-50%);
        font-size: 30px;
        color: white;
        background: transparent;
        border: none;
        cursor: pointer;
        z-index: 1001; // Ensure buttons are above the image
        width: 30px;
        height: 200px;
      }
  
      .prevBtn {
        left: 0px;
      }
  
      .nextBtn {
        right: 0px;
      }
    }
  }
}
